/* Developer */
.developer-text-container{
  color: white;
}

.developer-text-header {
  color: white;
  font-family: digital-7;
  font-size: 70px;
  text-align: center;
  margin: 40px 0px 15px 0;
}

.developer-text-paragraph {
  color: white;
  font-size: 20px;
}

.portfolio-container {
  display: flex;
  flex-direction:row;
  margin-top: 50px;
  margin-bottom: 50px;
  overflow: hidden; 
}

.portfolio-container-row {
  display: flex;
  flex-direction:row;
  position: relative;
  margin-top: 50px;
  margin-bottom: 50px;
  overflow: hidden; 
}

.dev-info {
  font-family: digital-7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: linear-gradient(var(--blue), var(--org));
  color: white;
  margin: 10px;
  margin-top: 10px;
  padding: 10px;
  border-radius: 25px;
  width: 100%;
  transition: transform .4s;
}

.dev-info:hover {
  transform: scale(1.1);
  transform-origin: 50% 50%;

}

.dev-info h1 {
  margin-top: 10px;
  font-size: 30px;
  text-align: center;
}

.dev-info a {
  text-decoration: none;
  color: white;
  flex-direction: row;
}

.dev-info i {
  font-size: 35px;
  margin-top: 10px;
  flex-direction: row;
  
}

.dev-icon-container {
  display: flex;
flex-direction: row;
}

.dev-icon {
display: flex;
flex-direction: row;
margin: 5px;
}

.dev-icon h4 {
  margin: 5px;
}

.dev-pic {
  width: 80%;
  border-radius: 10%;
  border: 3px solid var(--org);
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.line {
    border-top: 5px solid white;
    margin-top: 10px;
  }

@media screen and (max-width: 900px) {
  .developer-text-container{
    color: white;
  }

  .developer-text-header {
    font-size: 60px;
    margin: 40px 0px 15px 0;
  }

  .developer-text-paragraph {
    font-size: 20px;
  }
  .portfolio-container {
    display: inline-block;
    align-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2px;
  }

  .portfolio-container-row {
    flex-direction: column;
    align-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1px;
  }

  .dev-info {
    /* flex-direction: column; */
    overflow: hidden;
    width: 85%;
    height: 85%;
    
  }

  .dev-info:hover {
      transform: scale(1);
  }
}